<template>
  <div>
    <el-card class="wsy-card">
      <div class="el-card_body">
        <el-row>
          <el-col :span="12">
            <el-button :span="4" type="primary" @click="addRolesMotal = true">添加角色</el-button>
          </el-col>
        </el-row>
        <el-table border stripe :data="RolesList" style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-row
                  :class="['dbbottom',index === 0 ? 'dbtop':'']"
                  v-for="(item,index) in scope.row.children"
                  :key="index"
                >
                  <!-- <el-col :span="24"> -->
                  <el-col :span="5">
                    <el-tag
                      closable
                      @close="removeRightById(scope.row,item.id)"
                      :disable-transitions="false"
                    >{{item.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="19">
                    <el-row :class='[i === 0?"":"dbtop"]' v-for="(it,i) in item.children" :key="i">
                      <el-col :span="6">
                        <el-tag
                          closable
                          type="success"
                          @close="removeRightById(scope.row,it.id)"
                          :disable-transitions="false"
                        >{{it.authName}}</el-tag>
                        <i class="el-icon-caret-right"></i>
                      </el-col>
                      <el-col :span="18">
                        <el-tag
                          closable
                          @close="removeRightById(scope.row,t.id)"
                          type="warning"
                          :disable-transitions="false"
                          v-for="(t,d) in it.children"
                          :key="d"
                        >{{t.authName}}</el-tag>
                      </el-col>
                    </el-row>
                  </el-col>
                  <!-- </el-col> -->
                </el-row>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label="#" type="index"></el-table-column>
          <el-table-column label="角色名称" prop="roleName"></el-table-column>
          <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <div>
                <el-button
                  size="small"
                  type="primary"
                  icon="el-icon-edit"
                  @click="editRolesMotai(scope.row)"
                >编辑</el-button>
                <el-button
                  size="small"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeRolesMotal(scope.row.id)"
                >删除</el-button>
                <el-button
                  size="small"
                  type="warning"
                  icon="el-icon-setting"
                  @click="settingRolesMotai(scope.row)"
                >分配权限</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>

    <!-- 添加角色模态框 -->
    <el-dialog title="添加角色" :visible.sync="addRolesMotal">
      <el-form
        :model="addRolesformData"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addRolesformData.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addRolesformData.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="quitAddRoles()">取 消</el-button>
        <el-button type="primary" @click="addRoles()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改角色模态框 -->
    <el-dialog title="修改角色" :visible.sync="editRolesMotal">
      <el-form
        :model="editRolesformData"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editRolesformData.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editRolesformData.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="quitEditRoles()">取 消</el-button>
        <el-button type="primary" @click="EditRoles()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 分配权限模态框 -->
    <el-dialog title="分配权限" :visible.sync="settingRolesMotal" @close='seteRightDialogClosed'>
      <el-tree :data="rightslist" show-checkbox node-key='id' default-expand-all :default-checked-keys='defKeys' ref='treeRef' :props="treeProps" ></el-tree>
      <!-- 分配权限 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="settingRolesMotal = false">取 消</el-button>
        <el-button type="primary" @click="allotRight">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      // 添加角色模态框状态
      addRolesMotal: false,
      // 修改角色模态框状态
      editRolesMotal: false,
      // 分配权限模态框状态
      settingRolesMotal: false,
      RolesList: [],
      // 添加角色模态框表单数据
      addRolesformData: {
        roleName: "",
        roleDesc: ""
      },
      // 修改角色模态框表单数据
      editRolesformData: {
        roleId: 0,
        roleName: "",
        roleDesc: ""
      },
      // 删除角色ID
      removeRolesId: 0,
      //   添加角色模态框表单验证
      rules: {
        roleName: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "角色名称长度在2~8个字符之间",
            trigger: "blur"
          }
        ],
        roleDesc: [
          { required: true, message: "请输入角色描述", trigger: "blur" }
        ]
      },
      // 所有权限数据
      rightslist: [],
      // 树形控件属性绑定对象
      treeProps:{
        label:"authName",
        children:"children"
      },
      // 默认选中的节点值数组
      defKeys:[105,116],
      // 当前即将分配权限的角色ID
      roleId:''
    };
  },
  created() {},
  mounted() {
    this.getRolesList();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 获取角色列表
    getRolesList() {
      this.$axios.get("roles").then(({ data }) => {
        this.RolesList = data.data;
        // console.log(data.data);
      });
    },
    // 获取权限列表
    async getRights() {
      let { data } = await this.$axios.get("rights/tree");
      if (data.meta.status == 200) {
        this.$message({
          message: data.meta.msg,
          type: "success"
        });
        //  获取到的权限数据保存到data中
        this.rightslist = data.data;
      } else {
        return this.$message.error("获取权限数据失败");
      }
      console.log(this.rightslist);
    },
    // 添加角色
    addRoles() {
      this.$axios.post("roles", this.addRolesformData).then(({ data }) => {
        // console.log(data);
        if (data.meta.status == 201) {
          this.$message({
            message: data.meta.msg,
            type: "success"
          });
        } else {
          this.$message({
            message: data.meta.msg,
            type: "error"
          });
        }
        this.addRolesformData.roleName = "";
        this.addRolesformData.roleDesc = "";
        this.getRolesList();
        this.addRolesMotal = false;
      });
    },
    // 取消添加角色
    quitAddRoles() {
      this.addRolesMotal = false;
      this.addRolesformData.roleName = "";
      this.addRolesformData.roleDesc = "";
    },
    // 取消修改角色
    quitEditRoles() {
      this.editRolesformData.roleName = "";
      this.editRolesformData.roleDesc = "";
      this.editRolesMotal = false;
    },
    editRolesMotai(RoleInfo) {
      // 使用深拷贝的方式改变要修改的数据的指针
      this.editRolesformData = JSON.parse(JSON.stringify(RoleInfo));
      this.editRolesMotal = true;
    },
    // 修改角色
    async EditRoles() {
      let { data } = await this.$axios.put(
        `roles/${this.editRolesformData.id}`,
        {
          roleName: this.editRolesformData.roleName,
          roleDesc: this.editRolesformData.roleDesc
        }
      );
      if (data.meta.status == 200) {
        // this.editUserMotal = false;
        this.$message({
          message: data.meta.msg,
          type: "success"
        });
      } else {
        this.$message({
          message: data.meta.msg,
          type: "error"
        });
      }
      this.editRolesMotal = false;
      this.getRolesList();
    },
    // 删除角色模态框显隐
    removeRolesMotal(ID) {
      this.removeRolesId = ID;
      this.$confirm("此操作将永久删除该角色, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$axios.delete(`roles/${this.removeRolesId}`).then(({ data }) => {
            if (data.meta.status == 200) {
              this.getRolesList();

              this.$message({
                type: "success",
                message: data.meta.msg
              });
            } else {
              this.$message({
                type: "error",
                message: data.meta.msg
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      // this.getRolesList();
    },
    //  设置权限模态框显隐
    settingRolesMotai(role) {
      this.roleId = role.id
      // 使用深拷贝的方式改变要设置的数据的指针
      // this.settingRolesformData = JSON.parse(JSON.stringify(settingRole));
      // 获取所有权限数据
      this.getRights();
      // 递归获取三级节点
     this.getLeafKeys(role,this.defKeys)
      this.settingRolesMotal = true;
    },
    // 删除权限标签
    removeRightById(role, rightID) {
      // console.log(role,rightID);

      this.$confirm("此操作将永久删除该权限, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$axios
            .delete(`roles/${role.id}/rights/${rightID}`)
            .then(({ data }) => {
              if (data.meta.status == 200) {
                role.children = data.data;
                // this.getRolesList();
                this.$message({
                  type: "success",
                  message: data.meta.msg
                });
              } else {
                this.$message({
                  type: "error",
                  message: data.meta.msg
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 通过递归的形式，获取角色下的所有三级权限的ID，并保存到数组中
    getLeafKeys(node,arr){
      // 如果当前node节点不包含children属性，则是三级节点
      if(!node.children){
        return arr.push(node.id)
      }
      node.children.forEach(item=>{
        this.getLeafKeys(item,arr)
      })
    },
    // 监听分配权限对话框的关闭事件
    seteRightDialogClosed(){
      this.defKeys = []
    },
    // 点击确定为角色分配权限
  async  allotRight(){
      const keys = [
     ... this.$refs.treeRef.getCheckedKeys(),
     ... this.$refs.treeRef.getHalfCheckedKeys()
     ]
     const idStr = keys.join(',')
    const { data } = await this.$axios.post(`roles/${this.roleId}/rights`,{rids:idStr})
      if(data.meta.status == 200){
        this.$message.success("分配权限成功")
        this.getRolesList()
        this.settingRolesMotal = false
      }else{
        return this.$message.error("分配权限失败")
      }
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy-card {
  margin-top: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
  border: 1px solid #ebeef5;
  background-color: #fff;
  color: #303133;
  transition: 0.3s;
  border-radius: 4px;
  overflow: hidden;
  > .el-card_body {
    padding: 20px;
  }
}
.el-table {
  margin-top: 15px;
}
.el-tag {
  margin-top: 7px;
}
.dbtop {
  border-top: 1px solid #eee;
}
.dbbottom {
  border-bottom: 1px solid #eee;
}
</style>
